import React from 'react';
import styled from 'styled-components';
import {InnerLayout} from '../styles/Layouts';
import Title from '../Components/Title';
import ServiceCard from '../Components/ServiceCard';
import design from '../img/design.svg';
import intelligence from '../img/intelligence.svg';
import sound from '../img/sound.svg';

function ServicesSection() {
    return (
        <InnerLayout>
            <ServicesSectionStyled>
                <Title title={'Services'} span={'services'} />
                <div className="services" >
                    <div className='first-card' data-aos="fade-right" data-aos-delay="600" >
                    <ServiceCard 
                        image={design} 
                        title={'Web Design'} 
                        
                    />
                    </div>
                    <div className="mid-card" data-aos="fade-right" data-aos-delay="800">
                        <ServiceCard 
                            image={intelligence} 
                            title={'Circuit Design'} 
                            
                        />
                    </div>
                    <div className='last-card' data-aos="fade-right" data-aos-delay="1000">
                    <ServiceCard 
                        image={sound} 
                        title={'Audio Design'} 
                        
                    />
                    </div>
                </div>
            </ServicesSectionStyled>
        </InnerLayout>
    )
}

const ServicesSectionStyled = styled.section`
    .services{
        margin-top: 5rem;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 1.5rem;
        @media screen and (max-width:1000px){
            flex-direction: column;
        }
        @media screen and (max-width:950px){
            grid-template-columns: repeat(2, 1fr);
        }
        @media screen and (max-width:650px){
            grid-template-columns: repeat(1, 1fr);
        }
       
    }
`;

export default ServicesSection;
